  display: block;
  position: relative;
}
.sf-menu {
  margin-bottom: 1em;
}
.sf-menu a {
  color: #fff;
  display: block;
  padding: 15px 12px;
  line-height: normal;
  text-decoration: none;
}
.sf-menu li {
  background: transparent;
  white-space: nowrap;
  *white-space: normal;
  transition: background .2s;
}
.sf-menu li.current-menu-item,
.sf-menu li:hover,
.sf-menu li.sfHover {
  background: #006199;
  transition: none;
}
