.sf-menu li.sfHover a {
  color: #fff;
}
.sf-menu li.current-menu-item .sub-menu a,
.sf-menu li:hover .sub-menu a,
.sf-menu li.sfHover .sub-menu a {
  color: #fff;
}
.sf-menu .sub-menu {
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
  min-width: 120px;
}
.sf-menu .sub-menu li {
  background: #0081cc;
}
.sf-menu .sub-menu li a {
  padding-top: 8px;
  padding-bottom: 8px;
}
.sf-menu .sub-menu li.current-menu-item,
.sf-menu .sub-menu li:hover,
.sf-menu .sub-menu li.sfHover {
  background: #006199;
  transition: none;
}
.sf-menu .sub-menu li.current-menu-item a,
.sf-menu .sub-menu li:hover a,
.sf-menu .sub-menu li.sfHover a {
  color: #fff;
}
.sf-arrows .sf-with-ul {
  padding-right: 25px;
}
.sf-arrows .sf-with-ul:after {
  content: '';
  position: absolute;
  top: 50%;
  right: 8px;
  margin-top: -3px;
  height: 0;
  width: 0;
  border: 5px solid transparent;
  border-top-color: #fff;
  border-top-color: rgba(255, 255, 255, 0.5);
