/* --------------------------------------------------------------
 * = 简码样式
 * ----------------------------------------------------------- */
.toggle,
.accordion {
  margin-top: 1px;
  box-shadow: inset 1px 1px 1px 0 #cfcfcf;
  background: #fafafa;
  border-radius: 3px;
  border-bottom: 1px solid #e5e5e5;
  border-right: 1px solid #e8e8e8;
  overflow: hidden;
}
.toggle dt,
.accordion dt,
.toggle dd,
.accordion dd {
  padding-left: 15px;
  padding-right: 15px;
  border: 1px solid #d5d5d5;
}
.toggle dt,
.accordion dt {
  margin-top: -1px;
}
