/* --------------------------------------------------------------
 * = 弹出框
 * ----------------------------------------------------------- */
.blocker {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  z-index: 9;
  box-sizing: border-box;
  background-color: #000000;
  background-color: rgba(0, 0, 0, 0.3);
  text-align: center;
}
.blocker:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  margin-right: -0.05em;
}
.modal {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  text-align: left;
  z-index: 2;
  background: #fff;
  box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.25);
  width: 600px;
}
@media screen and (max-width: 48em) {
