}
.modal-open {
  overflow: hidden;
  overflow-y: scroll;
}
.modal-title {
  height: 42px;
  line-height: 42px;
  padding: 0 20px;
  font-size: 16px;
  border-bottom: 1px solid #ddd;
}
.modal-content {
  padding: 15px 20px;
}
.modal .close {
  position: absolute;
  top: 10px;
  right: 20px;
}
.modal-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(18, 18, 18, 0.3);
  z-index: 1001;
}
