/* --------------------------------------------------------------
 * = 评论样式
 * ----------------------------------------------------------- */
.wp-post-image {
  max-width: 100%;
  height: auto;
}
.alignnone {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
}
.aligncenter {
  display: block;
  margin: 15px auto;
}
.alignleft,
.alignright {
  margin-bottom: 15px;
}
@media (min-width: 64rem) {
  .alignleft {
    float: left;
    margin-right: 15px;
  }
  .alignright {
    float: right;
    margin-left: 15px;
  }
}
.wp-caption {
  display: block;
  margin-bottom: 23px;
  transition: border 0.2s ease-in-out;
}
.wp-caption-text {
  padding: 6px 10px;
  color: #fff;
  margin-top: -36px;
  background-image: linear-gradient(transparent 0, #000000 300%);
  position: relative;
}
.screen-reader-text {
  display: none;
}
.comments-title {
  margin-top: 30px;
}
.comment-list {
  margin-bottom: 40px;
  margin-left: 0;
  padding-left: 0;
}
.comment-list .avatar {
  border-radius: 3px;
  border: 1px solid #ddd;
}
.comment-list ol {
  list-style: none;
}
.comment-list .children {
  margin-left: 0;
  padding-left: 40px;
  background-repeat: no-repeat;
  background-position: left 20px;
}
.comment-list li.comment > div img.avatar {
  position: absolute;
  left: 29px;
  top: 29px;
}
.wp-caption {
  margin-bottom: 16px;
}
.comment .comment-body {
  overflow: hidden;
  padding: 25px 25px 25px 80px;
  margin: 2em 0 0;
  position: relative;
  border: 1px solid #EDEDED;
  background: #FBFCFD;
  border-radius: 3px;
  border-bottom: 1px solid #ddd;
}
.comment-author.vcard .avatar {
  position: absolute;
  left: 15px;
  top: 29px;
  border-radius: 2px;
}
.comment-author.vcard .says {
  padding-left: 2px;
}
.comment-metadata {
  font-size: 11px;
  line-height: 16px;
  margin-bottom: 10px;
}
.comment-metadata a {
  color: #999;
  font-size: 12px;
}
.comment-reply-link {
  color: #bbb;
}
.comment-reply-title {
  margin-top: 15px;
}
.comment-notes {
  color: #bbb;
}
.comment-content {
  margin-top: 10px;
}
.comment-navigation {
  overflow: hidden;
  margin-top: 30px;
}
.comment-form input[type="text"],
.comment-form input[type="email"],
.comment-form input[type="url"],
.comment-form textarea {
  width: 100%;
  height: auto;
}
.icon {
  background-image: url('../images/sprite.png');
}
/* --------------------------------------------------------------
* = 样式重置, 包括重置标签样式和框架通用类样式
* ----------------------------------------------------------- */
/* --------------------------------------------------------------
* = 通用样式, 设置网站全局通用样式
* ----------------------------------------------------------- */
.sep {
  margin-bottom: 15px;
}
.sep-big {
  margin-bottom: 40px;
}
a {
  color: #333333;
}
a:hover {
  color: #0081cc;
}
.row {
  position: relative;
}
.breadcrumbs {
  height: 38px;
  line-height: 38px;
  float: right;
  color: #999;
}
.breadcrumbs:before {
  content: "\f015";
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  margin-right: 5px;
  color: #0081cc;
}
.breadcrumbs a {
  color: #999;
}
.breadcrumbs a:hover {
  color: #0081cc;
}
/* --------------------------------------------------------------
* = 整体布局, 很多页面共享的页面框架
* ----------------------------------------------------------- */
@media screen and (max-width: 48em) {
  .site-main {
    margin-top: 15px;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.page-header {
  margin: 0 0 40px;
  padding: 20px 0;
  position: relative;
  background-color: #f7f7f7;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
}
.page-header .page-title {
  color: #0081cc;
}
.page-header .taxonomy-description p {
  margin-bottom: 0;
}
/* --------------------------------------------------------------
* = 页头, 网站顶部内容样式
* ----------------------------------------------------------- */
.site-header {
  background: #0081cc;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  padding: 30px 0;
  color: #fff;
}
.site-header .site-title {
  color: #fff;
}
.site-title a {
  color: #fff;
  font-size: 18px;
}
@media screen and (max-width: 48em) {
  .site-title {
    height: 44px;
    line-height: 44px;
  }
  .site-title a {
    line-height: 44px;
  }
}
/* --------------------------------------------------------------
* = 文章页排版
* ----------------------------------------------------------- */
.entry-meta {
  color: #777;
  font-size: 12px;
  margin-bottom: 10px;
  line-height: normal;
}
.entry-meta li {
  display: inline-block;
  margin: 0 15px 5px 0;
}
.entry-meta li .fa {
  color: #777;
  margin-right: 5px;
}
.entry-meta a {
  color: #777;
}
.single .entry-header,
.page .entry-header {
  margin-bottom: 20px;
  border-bottom: 3px solid #eeeeee;
}
.single .entry-header .entry-title,
.page .entry-header .entry-title {
  color: #000;
  font-size: 21px;
  font-weight: 500;
  margin-bottom: 10px;
}
.single .entry-content,
.page .entry-content {
  margin-bottom: 30px;
}
.single .entry-content h2,
.page .entry-content h2 {
  border-bottom: 1px dotted #ddd;
  color: #0081cc;
  font-size: 18px;
  padding-bottom: 6px;
}
.single .entry-content h3,
.page .entry-content h3 {
  color: #666;
  font-size: 16px;
  border-bottom: 1px solid #eee;
  padding-bottom: 5px;
}
.single .entry-content p,
.page .entry-content p {
  margin-bottom: 1em;
}
.single .entry-footer .entry-tags,
.page .entry-footer .entry-tags {
  margin-bottom: 10px;
}
.page-subtitle {
  background: #eee;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  padding: 15px 0;
  margin-bottom: 30px;
  margin-top: -40px;
}
.page-subtitle li {
  display: inline-block;
  margin-right: 15px;
}
.page-subtitle li.current_page_item a {
  color: #0081cc;
}
/* --------------------------------------------------------------
* = 首页, 首页独立样式
* ----------------------------------------------------------- */
.home .site-content {
  padding-top: 30px;
}
/* --------------------------------------------------------------
* = 存档页, 注意文章条目显示不要和文章页冲突, 注意文章类型和分类法
* ----------------------------------------------------------- */
.archive .site-main {
  background: transparent;
}
@media screen and (max-width: 48em) {
  .archive .site-main {
    padding: 15px;
  }
}
.archive .site-main .media {
  padding: 30px 0;
}
.archive .site-main .media + .media {
  margin-top: 0;
}
.archive .hentry {
  background: #fff;
  margin-bottom: 24px;
  padding-bottom: 24px;
  border-bottom: 1px solid #eeeeee;
}
.archive .entry-title {
  margin-top: 0;
  margin-bottom: 10px;
}
.archive .entry-content {
  position: relative;
}
.archive .entry-content h2 {
  border: 0;
  margin-top: 0;
  font-size: 1.4em;
}
.archive .entry-content p {
  margin-bottom: 0;
}
/* --------------------------------------------------------------
* = 边栏, 注意设置内置小工具样式
* ----------------------------------------------------------- */
.widget-area > .col {
  padding-left: 30px;
}
.widget {
  margin-bottom: 50px;
}
.widget a {
  color: #333333;
}
.widget a:hover {
  color: #0081cc;
}
.widget-title {
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 500;
  color: #000;
}
.widget_archive ul li,
.widget_categories ul li,
.widget_recent_comments ul li,
.widget_recent_entries ul li {
  padding: 6px 0;
  border-bottom: 1px solid #eeeeee;
}
.widget_archive ul li:first-child,
.widget_categories ul li:first-child,
.widget_recent_comments ul li:first-child,
.widget_recent_entries ul li:first-child {
  border-top: 1px solid #eeeeee;
}
.widget_archive a:before,
.widget_categories a:before,
.widget_recent_comments a:before,
.widget_recent_entries a:before {
  font-family: "FontAwesome";
  font-weight: normal;
  font-style: normal;
  letter-spacing: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  margin-right: 6px;
  color: #999;
  display: inline-block;
}
.widget_recent_entries a:before {
  content: "\f0f6";
}
.widget_recent_comments a:first-child:before {
  content: "\f0e5";
}
.widget_archive a:before {
  content: "\f016";
}
.widget_categories a:before {
  content: "\f114";
}
/* --------------------------------------------------------------
* = 页脚
* ----------------------------------------------------------- */
.site-footer {
  background: #fff;
  box-shadow: inset 0 1px 1px 0 rgba(0, 0, 0, 0.15);
  padding: 1.5em;
  margin-top: 30px;
}
.footer-navigation .menu-item {
  display: inline-block;
  margin-right: 15px;
}
/* --------------------------------------------------------------
* = 移动端样式
* ----------------------------------------------------------- */
/* --------------------------------------------------------------
* = 媒体查询, 注意 min 和 max 的区别, 注意屏幕宽度区别
* ----------------------------------------------------------- */
@media screen and (min-width: 48em) {
  .site {
    position: relative;
    overflow: hidden;
  }
}
@media screen and (max-width: 48em) {
  .site-header {
    padding: 0;
  }
}
